<template>
  <div class="user-data-manage">
    <div class="mani">
      <el-button size="small" type="info" @click="downList"
        ></el-button
      >

      <el-button size="small" type="info" @click="addTrlData"
        ></el-button
      >

      <el-input
        style="width: 300px; margin-left: 15px"
        placeholder=""
        v-model="searchStr"
        size="small"
        class="input-with-select"
      >
        <el-button
          slot="append"
          icon="el-icon-search"
          @click="searchClick"
        ></el-button>
      </el-input>
      <br />
      <br />
      <el-table :data="tableData" border style="width: 100%" height="470">
        <el-table-column fixed type="index" label="" width="80">
        </el-table-column>
        <el-table-column fixed prop="name" label="" width="180">
        </el-table-column>
        <el-table-column prop="ckid" label="" width="120">
        </el-table-column>
        <el-table-column prop="jing" label="" width="100">
        </el-table-column>
        <el-table-column prop="wei" label="" width="100">
        </el-table-column>
        <el-table-column prop="daid" label=""> </el-table-column>
        <el-table-column prop="company" label="" width="180">
        </el-table-column>
        <el-table-column prop="num" label="/">
        </el-table-column>
        <el-table-column prop="type" label=""> </el-table-column>
        <el-table-column prop="jtnum" label="/"> </el-table-column>
        <el-table-column prop="sctype" label=""> </el-table-column>
        <el-table-column prop="lev" label=""> </el-table-column>
        <el-table-column prop="dangerlev" label="" width="180">
        </el-table-column>
        <el-table-column prop="now" label=""> </el-table-column>

        <el-table-column label="" align="center" width="300">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="getMain(scope.row)"
              ></el-button
            >
            <el-button
              style="color: red"
              type="text"
              size="small"
              @click="delMain(scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>

    <el-dialog :title="jqMain.name" :visible.sync="dialogFormVisible">
      <p style="padding-bottom: 6px">{{ jqMain.ckid }}</p>
      <p style="padding-bottom: 6px">{{ jqMain.daid }}</p>
      <p style="padding-bottom: 6px">{{ jqMain.company }}</p>
      <p style="padding-bottom: 6px">/{{ jqMain.num }}</p>
      <p style="padding-bottom: 6px">{{ jqMain.type }}</p>
      <p style="padding-bottom: 6px">{{ jqMain.jtnum }}</p>
      <p style="padding-bottom: 6px">{{ jqMain.sctype }}</p>
      <p style="padding-bottom: 6px">{{ jqMain.lev }}</p>
      <p style="padding-bottom: 6px">{{ jqMain.dangerlev }}</p>
      <p style="padding-bottom: 6px">{{ jqMain.now }}</p>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ExcelJS from "exceljs";
import { saveAs } from "file-saver";
export default {
  components: {},
  data() {
    return {
      tableData: [],
      mainData: [],

      dialogFormVisible: false,
      form: {
        username: "",
        password: "",
        phone: "",
        licensePlate: [],
      },
      formLabelWidth: "120px",

      jqMain: {
        name: "",
        ckid: "C1000002009041120014651",
        jing: 113.090685,
        wei: 39.936395,
        daid: "143201016-005",
        company: "",
        num: 2500,
        type: "",
        jtnum: 9,
        sctype: "",
        lev: "",
        dangerlev: "",
        now: "",
      },

      searchStr: "",
    };
  },

  async created() {
    this.getData();
  },

  methods: {
    async getData() {
      var res = await this.$axios.get(
        "/DaTongMeiTanMarkerGet/DaTongMeiTanMarkerGet"
      );
      this.tableData = res.data.data;
      this.mainData = res.data.data;
      console.log(this.userList);
    },

    // 20240304 
    addTrlData() {
      this.$router.push("/HeritageProPush/add");
    },

    // 
    searchClick() {
      this.tableData = this.mainData;
      if (this.searchStr) {
        this.tableData = this.mainData.filter((ele) =>
          ele.name.includes(this.searchStr)
        );
      }
    },

    getMain(e) {
      this.jqMain = e;
      this.dialogFormVisible = true;
    },

    async delMain(e) {
      var res = null;
      res = await this.$axios.post(
        "/DaTongMeiTanMarkerRemove/DaTongMeiTanMarkerRemove",
        {
          _id: e._id,
        }
      );
      if (res.status === 200) {
        this.getData();
      }
    },
    editPark(e) {
      console.log(e);
      this.$router.push("/MeasureList");
    },

    async downList() {
      const workbook = new ExcelJS.Workbook();
      const worksheet = workbook.addWorksheet("Sheet1");

      // 
      worksheet.columns = [
        { header: "", key: "name" },
        { header: "", key: "ckid" },
        { header: "", key: "daid" },
        { header: "", key: "company" },
        { header: "/", key: "num" },
        { header: "", key: "type" },
        { header: "", key: "jing" },
        { header: "", key: "wei" },
        { header: "", key: "jtnum" },
        { header: "", key: "sctype" },
        { header: "", key: "lev" },
        { header: "", key: "dangerlev" },
        { header: "", key: "now" },
      ];

      // 
      for (let i = 0; i < this.mainData.length; i++) {
        worksheet.addRow(this.mainData[i]);
      }

      // 
      const buffer = await workbook.xlsx.writeBuffer();
      const blob = new Blob([buffer], {
        type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
      });

      const now = new Date();
      const year = now.getFullYear(); // 2025
      const month = now.getMonth() + 1; // 0~11  +1
      const day = now.getDate(); // 1~31
      const hours = now.getHours(); // 0~23
      const minutes = now.getMinutes(); // 0~59
      const seconds = now.getSeconds(); // 0~59
      var time = `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
      saveAs(blob, "" + time + ".xlsx");
    },
  },
};
</script>

<style>
.user-data-manage .mani {
  padding: 15px;
}
</style>
